<template>
  <div class="js-container">
    <div class="article-list">
      <ul>
          <li v-for="(item,index) in articleList" :key="index">
            <div @click="spread(index)">
              <Card class="article-info" title="true" id="article-info">
                <p slot="title">{{item.title}}</p>
                <p class="article-body">{{item.content}}</p>
              </Card>
            </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      articleList: []
    }
  },
  methods: {
    getList: function () {
      var _this = this
      _this.$axios.post('/jsPage', {page: 1}).then(res => {
        res.data.forEach(function (item) {
          _this.articleList.push({
            title: item.title,
            content: item.content,
            body: item.body
          })
        })
      })
    },
    spread: function (index) {
      var articleInfo = document.getElementsByClassName('article-info')
      var aritcleBody = document.getElementsByClassName('article-body')
      for (let i = 0; i < articleInfo.length; i++) {
        if (i !== index) {
          articleInfo[i].style.height = '300px'
          aritcleBody[i].innerHTML = this.aritcleList[i].content
        }
      }
      articleInfo[index].style.height = '500px'
      aritcleBody[index].innerHTML = this.articleList[index].body
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

<style>
.article-info {
  width: 600px;
  height: 300px;
  transition: height, 1s;
  margin: 30px auto;
  overflow: auto;
}
</style>



