import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    navSelect: 0
  },
  mutations: {
    navSelectChange (state, val) {
      state.navSelect = val
    }
  }
})

export default store



